@import '../../styles/variables';
.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    &__logo {
        height: 32px;
        text-decoration: none;
        display: flex;
        align-items: center;
    }
    &__location {
        height: 18px;
        text-decoration: none;
        display: flex;
        align-items: center;
    }
}

.logo__title {
    font-weight: bold;
    font-size: 30px;
    line-height: 35px;
    color: $color_main_accent;
}

.location__name {
    margin-left: 7px;
    font-size: 14px;
    line-height: 16px;
    color: $color_gray_dark;
    transition: all ease-in-out 0.3s;
    .header__location:hover & {
        color: $color_main_accent;
    }
}

.location__svg * {
    fill: transparent;
    stroke: $color_gray_dark;
    transition: all ease-in-out 0.3s;
    .header__location:hover & {
        stroke: $color_main_accent;
    }
}